<template>
    <div class="collectTopic">
          <div class="collectTopic-header">
            <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
            <div class="collectTopic-handle">
              <el-button @click="addMeeting">{{$t('colTopic.publish')}}</el-button>
            </div>
          </div>
          <div class="collectTopicTable">
              <table class="collectTopicTable-table">
                <tr>
                  <td>{{$t('colTopic.title')}}</td>
                  <td>
                    <input type="text" class="collectTopicTable-table-theme" v-model="addMeetingTitle"/>
                  </td>
                </tr>
                <tr>
                  <td>{{$t('colTopic.receiver')}}</td>
                  <td>
                      <el-tag :key="name" type="primary" v-for="(name, index) in recipients" :closable="true" :close-transition="false" @close="delUser(name, index)">
                          {{name}}
                      </el-tag> 
                      <span role='button' @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i><span class="collectTopicTable-table-add">{{$t('colTopic.selectStaff')}}</span></span>
                  </td>
                </tr>
                <tr>
                  <td>{{$t('colTopic.content')}}</td>
                  <td>
                    <quill-editor v-model="addMeetingContent" :options = "editorOption"></quill-editor>
                  </td>
                </tr>
              </table>
        </div>
        <personnel @userInfo = "userInfo" ref="userData"></personnel>
    </div>
</template>

<script>
  import Quill from 'quill';
  import { ImageResize } from '../packageJS/ImageResize.js';
  Quill.register('modules/imageResize', ImageResize);
  import {mapActions, mapGetters} from 'vuex'
  import personnel from '../personnelList/personnelList'
  import './collectTopic.scss'
  export default {
  name: 'collectTopic',
  data() {
      return {
        addMeetingTitle:'',
        addMeetingContent:'',
        addMeetingUserName:'',
        sliderHandle:true,
        recipients:[],
        recipientsID:[],
        editorOption: {
          theme: 'snow',
          modules: {
              history: {
                delay: 1000,
                maxStack: 50,
                userOnly: false
              },
              imageResize: {
                displaySize: true
              }
          }
        }
      }
	},
	methods: {
      addMeeting() {
          if(!this.addMeetingTitle || !this.addMeetingContent || !this.recipientsID.length || !this.addMeetingUserName){
            this.$message({
              type: 'error',
              message: this.$t('colTopic.msg')
            }) 
            return
          }
          let addMeeting = {
            title:this.addMeetingTitle,
            content:this.addMeetingContent,
            publisher:this.addMeetingUserName,
            personnel:this.recipientsID,
            groups:this.$route.params.id,
            state : 'false',
            type: '1'
          }
          if(!this.$route.query.meetingID){
            this.$http.AddMeeting(addMeeting)
              .then(res=>{
                this.$router.push({name:'Topic'})
              })
              return
          }
          this.$http.UpDataMeeting(this.$route.query.meetingID, addMeeting)
            .then(res=>{
                this.$router.push({name:'Topic'})
            })
      },
      userInfo(user){
        if(!this.recipients || !this.recipientsID){
              user.forEach((item) =>{
                  this.recipients.push(item.name)
                  this.recipientsID.push(item._id)       
              })
              return;
          }
          let names = [];
          let nameID = [];
          user.forEach((item) =>{
              names.push(item.name)
              nameID.push(item._id)         
          })
          for(let i = 0; i < names.length; i++){
              this.recipients.indexOf(names[i]) === -1 ? this.recipients.push(names[i]) : 0; 
              this.recipientsID.indexOf(nameID[i]) === -1 ? this.recipientsID.push(nameID[i]) : 0;
          }
         
      },
      delUser(name, index) {
          this.recipients.splice(index, 1)
          this.recipientsID.splice(index, 1)
          this.$refs.userData.deleteUser(index)
      }
  },
  components: {
      personnel,
      ImageResize
  },
	created(){
      this.addMeetingUserName = this.$http.GetCurrentUserInfo()._id
      if(!this.$route.query.meetingID || !this.addMeetingUserName) return;
      this.$http.GetMeeting(this.$route.query.meetingID)
      .then(res=>{
          this.addMeetingTitle = res.data.title
          this.addMeetingContent = res.data.content
          res.data.personnel.forEach((item) =>{
              this.recipients.push(item.name)
              this.recipientsID.push(item._id)  
          })
      })
	  }
  }

</script>



